import React, { useEffect } from 'react';
import './TodoForm.css';
import { Grid } from '@mui/material/'; 
import { TextField } from '@mui/material/'; 
import { Autocomplete } from '@mui/material/'; 

function TodoForm( { addTodo, setOpenModal, dafaultCat }) {

  const [newTodoValue, setNewTodoValue] = React.useState('');


    const onChange = (event) => {
        setNewTodoValue(event.target.value)
    }
    const onCancel = () => {
        setOpenModal(false);
    }
    const onSubmit = (event) => {
        event.preventDefault();
        setOpenModal(false);
        const catValue = document.getElementById("listaCat").value;
        addTodo(newTodoValue , catValue );
    }

    return (
        <form onSubmit={onSubmit}>
          <label className='tituloForm'>Agregar nuevo TODO</label>
          <input className="inputCrear"
            autoFocus
            value={newTodoValue}
            onChange={onChange}
            placeholder="Escribe tu nuevo TODO"
          />
          <Grid container spacing={1} sx={{marginTop:"7px"}}>
            <Grid item xs={12}>
              <Autocomplete
                id="listaCat"
                options={dafaultCat}
                renderInput={(params) => <TextField {...params} label="Categoria" />}
              />              
              
            </Grid>
            
          </Grid>

          <div className="TodoForm-buttonContainer">
            <button
              type="button"
              className="TodoForm-button TodoForm-button--cancel"
              onClick={onCancel}
              >
              Cancelar
            </button>
            <button
              type="submit"
              className="TodoForm-button TodoForm-button--add"
            >
              Aadir
            </button>
          </div>
        </form>
      );
}

export { TodoForm };