import React from 'react';
import './TodoSearch.css';
import { TextField } from '@mui/material/'; 
import { Autocomplete } from '@mui/material/'; 

function TodoSearch({ searchValue, setSearchValue, loading, setValorCat, dafaultCat }) {
  const onSearchValueChange = (event) => {
    setSearchValue(event.target.value)
  }
    

  return (
    <>
      <input 
        className="TodoSearch"
        placeholder="Buscar TODO" 
        value={searchValue}
        onChange={onSearchValueChange}  
        disabled={loading}
      />
      <Autocomplete
        id="listaCategorias"
        onChange={(event, newValue) => {
          setValorCat(newValue)
        }}
        options={dafaultCat}
        renderInput={(params) => <TextField sx={{backgroundColor: "#F9FBFC"}} {...params} label="Categoria" />}
        sx={{
          marginTop:"10px",
          width: "calc(100% - 82px)",
          marginLeft: "40px",
          
        }}
      />
    </>
    
  );
}

export { TodoSearch };