import React, { useEffect } from 'react';
import { useLocalStorage } from './useLocalStorage';

function useTodos() {

    const {
        item: todos,
        saveItem: saveTodos,
        loading,
        error,
       } = useLocalStorage('TODOS_V1', []);
       
      const [searchValue, setSearchValue] = React.useState('');
      const [openModal, setOpenModal] = React.useState(false);
      const [valorCat, setValorCat ] = React.useState('');

      const completedTodos = todos.filter(todo => todo.completed == true).length;
      const totalTodos = todos.length;
    
      let searchedTodos = []
    
      if (!searchValue.length >= 1){
        if (valorCat) {
          searchedTodos = todos.filter(todo => {
            return todo.cat == valorCat
          });

        }else{
          searchedTodos = todos;
        }
      }else{
        const catValue = valorCat || "todos";
        searchedTodos = todos.filter(todo => {
          const todoText = todo.text.toLowerCase();
          const searchText = searchValue.toLowerCase();
          if (catValue !== "todos") {
            return todoText.includes(searchText) && todo.cat == catValue;
          }else{
            return todoText.includes(searchText)
          }
        })
      }

    
      const addTodo = (text, newCatValue) => {
        const newTodos = [...todos]
        newTodos.push({
          completed: false,
          text: text,
          cat: newCatValue || "otros",
        });
        saveTodos(newTodos);
      }
      const completeTodo = (text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
        const newTodos = [...todos]
        newTodos[todoIndex].completed = newTodos[todoIndex].completed ? false : true;
        saveTodos(newTodos);
      }
    
      const deleteTodo = (text) => {
        const todoIndex = todos.findIndex(todo => todo.text === text);
        const newTodos = [...todos]
        newTodos.splice(todoIndex, 1);
        saveTodos(newTodos);
      }

      const categoriasLista = () => {
        let catArreglo = [];
        catArreglo = todos.map(e => e.cat)
        catArreglo =  [...new Set(catArreglo)]
        return catArreglo
      }

    return {
            error,
            loading,
            totalTodos,
            completedTodos,
            addTodo,
            searchValue,
            setSearchValue,
            searchedTodos,
            completeTodo,
            deleteTodo,
            openModal,
            setOpenModal,
            categoriasLista,
            setValorCat,
        };    
}

export { useTodos };